import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import faces from '@/assets/face/faces'
export default {
  data () {
    return {
      swiperThemeOption: {
        slidesPerColumn: 2,
        slidesPerView: 4,
        freeMode: true,
        pagination: {
          el: '.swiper-pagination'
        }
      },
      list: faces
    }
  },
  methods: {
    clickFace (item) {
      this.$emit('onFaceClick', '[' + item.value + ']')
    }
  },
  components: {
    swiper,
    swiperSlide
  }
}



